.hover-button {
  position: relative;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: #ffc666;
  border: none;
  color: white;
  outline: none;
  overflow: hidden;
}
.hover-button span {
  position: relative;
  pointer-events: none;
}
.hover-button::before {
  --size: 0;
  content: "";
  position: absolute;
  left: var(--x);
  top: var(--y);
  width: var(--size);
  height: var(--size);
  background: radial-gradient(circle closest-side, #ffc666, transparent);
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-transition: width 0.2s ease, height 0.2s ease;
  transition: width 0.2s ease, height 0.2s ease;
}
.hover-button:hover::before {
  --size: 400px;
}
